*,
*::after,
*::before {
  box-sizing: border-box;
}

:root {
  font-size: 20px;
}

body {
  margin: 0;
  --color-text: rgb(255, 255, 255);
  --color-bg: #111111;
  --color-link: #d02c0d;
  --color-link-hover: rgb(218, 218, 218);
  color: var(--color-text);
  background-color: var(--color-bg);
  font-family: roc-grotesk, -apple-system, BlinkMacSystemFont, Segoe UI,
    Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --cursor-stroke: rgb(255, 255, 255);
  --cursor-fill: none;
  --cursor-stroke-width: 1px;
  line-height: 1;
  font-weight: 300;
  overflow: hidden;
  overflow-y: scroll;
}

.oh {
  overflow: hidden;
}

/* Page Loader */
.js .loading::before,
.js .loading::after {
  content: '';
  position: fixed;
  z-index: 3000;
}

.js .loading::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-bg);
}

.js .loading::after {
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  border-radius: 50%;
  opacity: 0.4;
  background: var(--color-link);
  animation: loaderAnim 0.7s linear infinite alternate forwards;
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}

a {
  text-decoration: none;
  color: var(--color-link);
  outline: none;
}

a:hover {
  color: var(--color-link-hover);
  outline: none;
}

/* Better focus styles from https://developer.mozilla.org/en-US/docs/Web/CSS/:focus-visible */
a:focus {
  /* Provide a fallback style for browsers
	 that don't support :focus-visible */
  outline: none;
  background: lightgrey;
}

a:focus:not(:focus-visible) {
  /* Remove the focus indicator on mouse-focus for browsers
	 that do support :focus-visible */
  background: transparent;
}

a:focus-visible {
  /* Draw a very noticeable focus style for
	 keyboard-focus on browsers that do support
	 :focus-visible */
  outline: 2px solid red;
  background: transparent;
}

.unbutton {
  background: none;
  border: 0;
  padding: 0;
  margin: 0;
  font: inherit;
}

.unbutton:focus {
  outline: none;
}

main {
  padding: 3rem 2rem;
}

.cursor {
  display: none;
}

.frame {
  position: relative;
}

.frame--footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 40vh;
}

.frame__title-wrap {
  width: 80%;
  max-width: 600px;
}

.frame__title {
  font-size: 2.3rem;
  margin: 0;
}

.frame__tagline {
  margin: 2rem 0 2.5rem;
  line-height: 1.2;
  max-width: 45ch;
}

.frame__links a:not(:last-child) {
  margin-right: 1rem;
}

.heading {
  font-family: kudryashev-d-excontrast-sans, sans-serif;
  font-weight: 300;
  font-size: 6vw;
  font-size: clamp(2rem, 6vw, 5rem);
  line-height: 1.1;
}

.heading .splitting .word,
.heading .splitting .char {
  display: flex;
}

.char-wrap {
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.char {
  display: inline-block;
  will-change: transform;
}

.heading > span {
  display: flex;
}

.heading > span:first-child {
  font-size: 14vw;
  font-size: clamp(2rem, 14vw, 9rem);
}

.heading > span:nth-child(2) {
  margin-top: -2vw;
  text-transform: lowercase;
}

.item {
  margin: 15vh 0 20vh;
}

.item__imgwrap,
.content__imgwrap {
  overflow: hidden;
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 200px;
  will-change: transform;
}

.item__img,
.content__img {
  width: 100%;
  height: 100%;
  min-height: 200px;
  background-size: cover;
  background-position: 50% 50%;
  will-change: transform;
}

.item__meta {
  margin: 0.85rem 0 2rem;
  line-height: 1.3;
  font-size: 0.67rem;
  text-transform: uppercase;
  font-weight: 500;
  overflow: hidden;
}

.item__meta-row {
  display: block;
  position: relative;
  overflow: hidden;
}

.item__meta-row span {
  display: block;
  will-change: transform;
}

.item__enter {
  display: none;
  will-change: transform;
}

.item__enter-circle {
  fill: none;
  stroke: #000;
  stroke-width: 1px;
  will-change: transform, opacity;
}

.item__excerpt {
  line-height: 1.2;
  will-change: transform;
}

.item__excerpt-link {
  display: inline-block;
  position: relative;
  cursor: pointer;
  color: var(--color-text);
  font-weight: 500;
  line-height: 1;
  overflow: hidden;
}

.item__excerpt-link span {
  display: inline-block;
  padding-bottom: 10px;
  position: relative;
}

.item__excerpt-link span::after {
  content: '';
  width: 100%;
  height: 2px;
  top: calc(100% - 10px);
  position: absolute;
  left: 0;
  background: var(--color-text);
  transition: transform 0.2s;
  transform-origin: 100% 0;
}

.item__excerpt-link span:hover::after {
  transform: scale3d(0, 1, 1);
}

.content {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  z-index: 2000;
  pointer-events: none;
}

.content .heading {
  margin-bottom: 5rem;
  font-size: 10vw;
  font-size: clamp(2rem, 10vw, 6rem);
}

.content .heading > span:first-child {
  font-size: 14vw;
  font-size: clamp(2rem, 14vw, 10rem);
}

.content__article {
  height: 0;
  overflow: hidden;
}

.content__article--open {
  pointer-events: auto;
  padding: 0 0 20vh;
  height: 100%;
  overflow: hidden;
}

.content__article .heading {
  text-align: center;
}

.content__text {
  padding: 0 1rem;
  line-height: 1.2;
  margin: 0 auto;
  max-width: 65ch;
  will-change: transform;
}

.content__text p {
  margin: 1.5rem 0;
}

.content__text img {
  max-width: 50%;
  margin: 5rem auto;
  display: block;
  border-radius: 50%;
}

.content__back {
  position: absolute;
  top: 2rem;
  left: 2rem;
  opacity: 0;
  cursor: pointer;
}

.content__article--open ~ .content__back {
  pointer-events: auto;
}

@media screen and (min-width: 53em) {
  .item {
    position: relative;
    display: grid;
    grid-template-areas:
      'image image image enter'
      'meta meta meta meta'
      '... ... excerpt ...';
    grid-template-columns: 1fr auto 360px 1fr;
    grid-template-rows: minmax(420px, 40vh) auto auto;
  }
  .item--invert {
    grid-template-areas:
      'enter image image image'
      'meta meta meta meta'
      '... ... excerpt ...';
    grid-template-columns: 1fr auto 360px 1fr;
  }
  .item__imgwrap {
    grid-area: image;
  }
  .item__img {
    width: 100%;
    height: 100%;
  }
  .item__enter {
    align-self: center;
    justify-self: center;
    display: flex;
    grid-area: enter;
    position: relative;
    z-index: 1000;
    max-width: 400px;
    max-height: 400px;
    align-items: center;
    justify-content: center;
  }
  .item__enter-circle {
    pointer-events: none;
    cursor: pointer;
    flex: none;
  }
  .item__meta {
    grid-area: meta;
  }
  .item--invert .item__meta {
    justify-self: end;
    text-align: right;
  }
  .item__excerpt {
    grid-area: excerpt;
  }
  .item__excerpt p {
    margin: 0 0 1.5rem;
  }
  .heading > span {
    justify-content: center;
  }
  .heading--item {
    margin: 0;
    grid-area: 1 / 1 / 2 / 5;
    justify-self: center;
    align-self: center;
    text-align: center;
  }
}

@media (any-pointer: fine) {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
    z-index: 5000;
  }

  .cursor__inner {
    fill: var(--cursor-fill);
    stroke: var(--cursor-stroke);
    stroke-width: var(--cursor-stroke-width);
    opacity: 0.7;
  }

  .no-js .cursor {
    display: none;
  }
}

/*! locomotive-scroll v4.1.1 | MIT License | https://github.com/locomotivemtl/locomotive-scroll */
html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}

[data-scroll-direction='horizontal'] [data-scroll-container] {
  height: 100vh;
  display: inline-block;
  white-space: nowrap;
}

[data-scroll-direction='horizontal'] [data-scroll-section] {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  height: 100%;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}
[data-scroll-direction='horizontal'] .c-scrollbar {
  width: 100%;
  height: 10px;
  top: auto;
  bottom: 0;
  transform: scaleY(1);
}
[data-scroll-direction='horizontal'] .c-scrollbar:hover {
  transform: scaleY(1.3);
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
[data-scroll-direction='horizontal'] .c-scrollbar_thumb {
  right: auto;
  bottom: 0;
}
